body {
  margin: 0;
  padding: 0;
}


html {
    background-color: red;
}

.page {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.fade-appear, .fade-enter {
    opacity: 0;
    z-index: 1;
}

.fade-appear-active, .fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    transition: all 625ms linear 625ms;
}

.fade-exit {
    opacity: 1;
    z-index: 0;
}

.fade-exit.fade-exit-active {
    opacity: 0;
    transition: all 1000ms linear;
}
.homeBtn {
    position: relative;
    background-color: #FF7804;
    font-size: 2.5rem;
    color: white;
    border: 0.1875rem solid #FF3105;
    padding: 0.5rem 1.20rem;
    transition: all 0.5s;
    overflow: hidden;
    font-family: 'Schoolbell', cursive;
    /* margin-left: 10rem; */
}

.homeBtn:before {
    content: '';
    background-color: white;
    position: absolute;
    top: 1rem;
    left: 0;
    width: 100%;
    height: 50%;
    -webkit-transform: translateX(-100%) rotate(45deg);
            transform: translateX(-100%) rotate(45deg);
    transition: all 0.5s;
    z-index: 1;
}

.homeBtn:hover {
    background-color: #FF3105;
}

.homeBtn:hover:before {
    -webkit-transform: translateX(100%) rotate(45deg);
            transform: translateX(100%) rotate(45deg);
}
